<!--1幅图-->
<template>
  <div class="course-list1">
    <a v-for="item in courses" class="course-item" :href="'#/detail/'+item.cid">
      <img class="responsive-img" :src="baseurl+(item.picture?item.picture:item.course_img)">
      <div class="other">
        <h5 class="title" v-text="item.title"></h5>
        <div v-text="item.teachers"></div>
      </div>
    </a>
  </div>
</template>

<script>
    import config from '../../../api/config'
    export default{
      data(){
        return {
          baseurl:config.baseurl
        }
      },
      props:{
        courses:{
          type: Array,
          default: []
        }
      }
    }
</script>

<style  lang="less" rel="stylesheet/less">


</style>



