<template>
  <div class="user-page">
    <div class="category"><span>个人中心</span></div>
    <div class="user-header">
      <yd-flexbox  direction="vertical">
        <yd-flexbox-item>
          <img :src="user.user_img" alt="">
          <div v-text="user.nickname">
          </div>
        </yd-flexbox-item>
      </yd-flexbox>
    </div>
    <yd-cell-group class="user-group">
      <yd-cell-item class="user-cell" arrow type="link" href="/profile">
        <span slot="left">个人资料</span>
      </yd-cell-item>
      <yd-cell-item class="user-cell" arrow type="link" href="/account">
        <span slot="left">账号绑定</span>
      </yd-cell-item>
      <yd-cell-item class="user-cell" arrow type="link" href="">
        <span slot="left">修改密码</span>
      </yd-cell-item>
      <yd-cell-item class="user-cell" arrow type="link" href="/opinion">
        <span slot="left">意见反馈</span>
      </yd-cell-item>
    </yd-cell-group>
  </div>
</template>

<script>
  export default{
    computed:{
      user(){
        return this.$store.state.user
      }
    }
  }
</script>

<style  lang="less" rel="stylesheet/less">
  @import "../../style/config.less";
  .user-page{
    background-color: rgb(40,74,112);
    padding:20px 0px;
    margin-bottom:-.5rem;
  .category{
    position: relative;
    text-align:center;
    margin:0 20px;
    span{
      display: inline-block;
      padding: 0 30px;
      font-size: .3rem;
      position: relative;
      z-index:2;
      background-color: rgb(40,74,112);
      color:#fff;
    }
    &:after{
       content: '';
       position: absolute;
       height: 1px;
       background-color: #fff;
       width: 100%;
       left:0;
       top:50%;
       transform: translateY(-50%);
       z-index: 1;
     }
  }
  .user-group{
     margin:0 40px;
    .m-cell a.cell-item, .m-cell label.cell-item,.m-cell{
      background-color: transparent;
    }
    .cell-left{
      color:#fff;
    }
    .cell-item:not(:last-child):after,.m-cell:after{
      display: none;
    }
  }
  }
</style>



