<template>
  <section class="search-bar" @click="show">
    <i class="icon iconfont icon-sousuo"></i>
    <div  >集智AI学园</div>
  </section>
</template>

<script>
    export default {
      name: 'search-bar',
      methods: {
        show () {
          this.$store.commit('showSearch')
        }
      }
    }
</script>

<style lang="less" rel="stylesheet/less">
  .search-bar{
    display: flex;
    margin: 10px;
    border:1px solid #555;
    border-radius:15px;
    align-items: center;
    padding: 5px;
    &>div,&>input{
      flex:1;
      border:none;
      outline: none;
      margin-left: 10px;
    }
    &.inner{
    background-color: #fff;
    border-radius: 5px;
     }
  }

</style>



