import vuex from 'vuex'
import vue from 'vue'
vue.use(vuex)
export  default new vuex.Store({
  state:{
    isSearch:false,
    isSheet:false,
    sheetData:[],
    user:{},
    discover:{},
    discoverTypes:[]  
  },
  mutations:{
     showSearch (state) {
       state.isSearch = true
     },
     hideSearch (state) {
       state.isSearch = false
     },
     showFilter (state, data){
       state.isSheet = true
       state.sheetData = data
     },
     hideFilter (state) {
       state.isSheet = false
     },
     setUser(state,user){
       state.user=user;
     }
  }
})



