<template>
  <section>
    <filter-bar :filterData="types"></filter-bar>
    <div class="course-list1">
      <a v-for="item in courses" class="course-item" :href="'#/video/'+item.cid">
        <img class="responsive-img" :src="baseurl+item.picture">
        <div class="other">
          <h5 class="title" v-text="item.title"></h5>
          <div v-text="item.teachers"></div>
        </div>
      </a>
    </div>
    <user ></user>
  </section>
</template>

<script type="text/ecmascript-6">
  import User from '../user/User.vue'
  import List from './templates/CouseList1.vue'
  export default{
    data(){
      return {
        types: [
          {
            label: '所有课程',
            method: () => {
              this.status=0
              this.getCourses()
            }
          },
          {
            label: '直播课程',
            method: () => {
              this.status=2
              this.getCourses()
            }
          },
          {
            label: '录播课程',
            method: () => {
              this.status=1
              this.getCourses()
            }
          }
        ],
        status:0,
        courses: [],
        user:{}
      }
    },
    components: {
      List,
      User
    },
    created () {
      this.getCourses()
      this.getUser()
    },
    methods:{
      getCourses(){
        this.$api.mycourse({perpage:10,page:1,status:this.status}).then(res => {
          if(res.data&&res.data.code==1){
            this.courses=res.data.msg
          }

        })
      },
      getUser(){
        var self=this;
        this.$api.userdetail().then(res => {
          self.$store.commit('setUser',res.data.msg)
          console.log(res.data.msg)
        })
      }
    }
  }
</script>

<style scoped lang="less">

</style>



