import Vue from 'vue'
import Router from 'vue-router'
import Index from '../page/index/Index.vue'
const Detail = r => require.ensure([], () => r(require('../page/course/Detail.vue')))
const Study = r => require.ensure([], () => r(require( '../page/course/Video.vue')))
const Search = r => require.ensure([], () => r(require( '../page/discover/Search.vue')))
const DiscoverList = r => require.ensure([], () => r(require( '../page/discover/DiscoverList.vue')))
const Profile = r => require.ensure([], () => r(require( '../page/user/Profile.vue')))
const Account = r => require.ensure([], () => r(require( '../page/user/Account.vue')))
const Opinion = r => require.ensure([], () => r(require( '../page/user/Opinion.vue')))
Vue.use(Router)
var routers = [
  {
    path: '/',
    redirect: '/index'
  },
  {
    name: 'index',
    path: '/index',
    component: Index
  },
  {
    name: 'detail',
    path: '/detail/:id',
    component: Detail
  },
  {
    name: 'video',
    path: '/video/:id',
    component: Study
  },
  {
    name: 'search',
    path: '/search',
    component: Search
  },
  {
    name: 'discoverlist',
    path: '/discoverlist/:id',
    component: DiscoverList
  },
  {
    path:'/profile',
    component:Profile
  },{
    path:'/account',
    component:Account
  },
  {
    path:'/opinion',
    component:Opinion
  }
]

export default new Router({
  routes: routers
})



