<template>
    <yd-actionsheet class="filter-sheet" :items="items" v-model="$store.state.isSheet"></yd-actionsheet>
</template>

<script type="text/ecmascript-6">
  export default{
    name: 'filter-sheet',
    data () {
      return {
        show: false
      }
    },
    computed: {
      /*show () {
        return this.$store.state.isSheet
      },*/
      items () {
        return this.$store.state.sheetData
      }
    },
    watch: {
      show(n, o){
        if (!n) {
          this.$sotre.commit('hideSheet')
        }
      }
    }
  }
</script>

<style scoped lang="less">

</style>



