<template>
  <section>
    <section style="padding-top:10px;">
      <swiper v-for="category in categorys" class="discover-swiper" :key="category.id"  :options="swiperOption">
        <swiper-slide @click.native="gotolist(category)" class="flexbox-v" :style="{'background-color':category.picture}">
          {{category.title}}
          <!--<a :href="'#/discoverlist/'+category.id" class="abs-link"></a>-->
        </swiper-slide>
        <swiper-slide v-for="course in category.course" :key="category.cid" class="flexbox-v">
          <div class="cover-bg" :style="{'background-image': 'url('+baseurl+course.picture+')'}"></div>
          <div class="flex content flexbox-v">
            <div class="flex">
              <div class="name">{{course.title}}</div>
              <div class="teacher">{{course.teachers}}</div>
            </div>
            <div class="time">{{course.during}}</div>
          </div>
          <a :href="'#/detail/'+course.cid" class="abs-link"></a>
        </swiper-slide>
      </swiper>

    </section>
    <search-bar></search-bar>
  </section>
</template>

<script type="text/ecmascript-6">

  require('swiper/dist/css/swiper.css')
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  export default {
    name: 'discover',
    data () {
      return {
        categorys:[],
        swiperOption: {
          loop: false,
          slidesPerView: 2,
          slidesPerColumn: 1,
          slidesOffsetBefore : 10,
          slidesOffsetAfter : 10,
          spaceBetween: 10
        },
        perpage:10,
        page:1
      }
    },
    created () {
      this.getData()
    },
    components: {
      swiper,
      swiperSlide
    },
    methods:{
      getData(){
        this.$api.category({perpage:this.perpage, page:this.page}).then((res)=>{
          if(res.data&&res.data.code==1){
              this.categorys=res.data.msg
          }
        })
      },
      gotolist(item){
        this.$store.state.discover=item
        this.$store.state.discoverTypes=this.categorys
        this.$router.push('/discoverlist/'+item.id)
      }
    }

  }
</script>

<style lang="less" scoped>

  .discover-swiper{
    margin-bottom: 10px;
  }
  .discover-swiper .swiper-slide{
    height: 4.1rem;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
    border:1px solid #ddd ;
  }
  .discover-swiper .cover-bg{
    height: 1.75rem;
    width: 100%;
  }
  .discover-swiper .content{
    padding: 0.15rem;
    width: 100%;
    .name{
      height: 0.5rem;
      margin-bottom: 0.3rem;
      font-size: 0.2rem;
    }
    .teacher{
      font-size: 0.18rem;
    }
    .time{
      font-size: 0.14rem;
    }
  }
  .discover-swiper .swiper-slide:nth-child(1){
     font-size: 0.36rem;
     color:#fff;
     background-color: lightsalmon;
     border-radius: 0;
    text-align: center;
  }
  .discover-swiper:nth-child(1) .swiper-slide:first-child{
    background-color: #89ba23;
  }
  .discover-swiper:nth-child(2) .swiper-slide:first-child{
    background-color: #e38e19;
  }
  .discover-swiper:nth-child(3) .swiper-slide:first-child{
    background-color: #374852;
  }
</style>



