let base = '';
if (process.env.NODE_ENV == 'development') {
  base='/mobileinterface'
}else if(process.env.NODE_ENV == 'production'){
  base = 'http://demo.swarma.org/mobileinterface'
}
export default {
  baseurl:'http://demo.swarma.org',
  mycourse: `${base}/mycourse`,
  userdetail: `${base}/userdetail`,
  category: `${base}/category`,
  video: `${base}/video`,
  coursedetail: `${base}/coursedetail`,
  cintroduce:`${base}/cintroduce`,
  classifiedcourse:`${base}/classifiedcourse`,
  search:`${base}/search`,
  moddetail:`${base}/moddetail`,
  searchinterface:`${base}/searchinterface`,
 
}



