import api from './config'
import axios from 'axios'
import qs from 'qs'
export default {
  post (api, data) {
    return axios.post(api[api], qs.stringify(data))
  },
  mycourse (data) {
    return axios.get(api.mycourse, {params:data})
  },
  userdetail (data) {
    return axios.post(api.userdetail, data)
  },
  category (data){
    return axios.post(api.category, data)
  },
  coursedetail(data){
    return axios.post(api.coursedetail, data)
  },
  cintroduce(data){
    return axios.get(api.cintroduce, {params:data})
  },
  search(data){
    return axios.post(api.search,data)
  },
  classifiedcourse(data){
    return axios.get(api.classifiedcourse,{params:data})
  },
  searchinterface(data){
    return axios.get(api.searchinterface,{params:data})
  },
  video(data){
    "use strict";
    return axios.get(api.video,{params:data})
  }
}



