<template>
  <yd-navbar :class="{nobg:nobg}" :bgcolor="bgcolor" color="#fff" :title="title">
    <a @click.prevent="$router.go(-1)" slot="left" >
      <yd-navbar-back-icon color="#fff"></yd-navbar-back-icon>
    </a>
  </yd-navbar>
</template>

<script>
    export default{
      name:'navbar',
      props:{
        title:{
          type:String
        },
        bgcolor:{
          type:String,
          default:'#266dbf'
        },
        nobg:{
          type:Boolean,
          default:false
        }
      }
    }
</script>

<style >
  .nobg{
    width: 100%;
    position: absolute;
    left:0;
    top:0;
  }
</style>



