<template>
  <div class="filter-btn">
    <i class="iconfont icon icon-caidan" @click="show">
    </i>
  </div>
</template>

<script>
    export default{
      name: 'filter-bar',
      props: {
        filterData: {
          type: Array,
          default: []
        }
      },
      methods: {
        show () {
          this.$store.commit('showFilter',this.filterData)
        }
      }
    }
</script>

<style scoped lang="less" rel="stylesheet/less">
  @import "../style/config.less";
  .filter-btn{
    padding: 10px 20px;
    text-align: right;
  .iconfont{
    font-size: 20px;
    color:@skin-color;
    }
  }
</style>



